.App {
  text-align: center;
}
:root{
  --black:#111517;
  --white:white;
  --background: #fafafa;
}
body{
  font-family: 'Nunito Sans', sans-serif;
  
  
}
main{
  background: var(--background);
  min-height: 100vh;
  color:var(--black);
}
.is-dark-mode {
  --black:white;
  --white:#273542;
  --background: #1a2833;
}

.is-light-mode {
    --black:#111517;
    --white:white;
    --background: #fafafa;
}

@media (prefers-color-scheme: dark) {
  :root {
    --black:white;
    --white:#273542;
    --background: #1a2833;
  }
}